  .btnExit .iconSidebar {
    margin: 0;
  }
  .iconImg{
    border-radius: 20px;
    height: 40px;
    margin-right:15px;
    margin-bottom: 10px;
    margin-top:10px;
  }
  .infoUser{
   
   justify-self: center;
    align-items: center;
    display: grid;
    margin-bottom: 20px;
  }
  .sidebar .logo-details .img{
    opacity: 1;
  }
  .sidebar .logo-details .logo_name {
    color: whitesmoke;
    font-size: 20px;
    font-weight: 800;
    opacity: 1;   
    
    
  }
  
  .sidebar .logo-details #btn{
    position: absolute;
    top: 50%;
    right: 0;
    font-size: 22px;
    font-size: 23px;
    text-align: center;
    cursor: pointer;
    
  }

  .homeicon{
    
    height: 36px;
    
    margin-right: 10px;
  }

  .iconSidebar{
    height: 24px;
    width: 24px;
    margin-top:4px;
    
    border-radius: 6px;
    font-size: 15px; 
  }
    
  .sidebar .nav-list{
    margin-top: 20px;
    height: calc(100%-200px);
    align-items: start;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
    text-decoration: none;
    
  }
  .logoutsection button{
 
    font-size:16px;
  }
  .sidebar li, .logoutsection li{
    
    margin: 0px 0px;
    list-style: none;
    border-radius: 1px;
    height: 100%;
    
  }
  .sidebar li a, .logoutsection a{
    display: flex;
    height: 100%;
    width: 100%;
    border-radius: 4px;
    justify-content: space-evenly;
    text-decoration: none;
    
   
    
    
    background: #1960c9;
    margin-bottom: 10px;
    align-items: center;
  
  }

  .sidebar li :hover{
    background:#0d6efd;

    /* background:#0d6efd;
    background: #1960c9;*/
    
  }
  .logoutsection{
      position: relative;
      margin-top: 240px;
      font-size: small;
  }
  .logoutsection a:hover{
    background: #0d6efd;
}

.links_name{
    color: whitesmoke;
    font-family: sans-serif;
}
 
